<template>
  <div class="tracker">
    <div class="counter">
      <div class="buttons">
        <button @click="increaseResource(resource, -1)">-1</button>
        <button @click="increaseResource(resource, -5)">-5</button>
      </div>
      <div :class="resource" class="total">{{ total }}</div>
      <div class="buttons">
        <button @click="increaseResource(resource, 1)">+1</button>
        <button @click="increaseResource(resource, 5)">+5</button>
      </div>
    </div>

    <div class="counter">
      <div class="buttons">
        <button @click="increaseProduction(resource, -1)">-1</button>
        <button @click="increaseProduction(resource, -5)">-5</button>
      </div>
      <div class="total production">{{ production }}</div>
      <div class="buttons">
        <button @click="increaseProduction(resource, 1)">+1</button>
        <button @click="increaseProduction(resource, 5)">+5</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "ResourceCounter",
  props: {
    total: Number,
    production: Number,
    resource: String,
  },
  methods: {
    increaseResource(res, inc) {
      this.$emit("increased-resource", res, inc);
    },
    increaseProduction(res, inc) {
      this.$emit("increased-production", res, inc);
    },
  },
};
</script>

