<template>
  <div id="app">

    <div class="board">

      <div class="header">
        <div class="rating">
            <button @click="rating--">-1</button>
          <div class="total">{{ rating }}</div>
            <button @click="rating++">+1</button>
        </div>

        <div class="generation">
          <div class="total">{{ current_generation}}</div>
            <button @click="nextGeneration">+1</button>
        </div>
      </div>

      <div class="counters">
        <ResourceCounter
          v-for="(res, index) in Object.keys(resources)"
          :key="index"
          @increased-resource="increaseResource"
          @increased-production="increaseProduction"
          :total="resources[res]"
          :production="production[res]"
          :resource="res"
        />
      </div>

      <div class="converters">
        <button class='converter oxygen-converter' @click='greensToOxygen'>Plants to O<sub>2</sub></button>
        <button class='converter heat-converter' @click='heatToTemp'>Heat to °C</button>
      </div>

      <div class="generations">
        <table>
          <tr>
            <td @click='selectGeneration(-1)'>prev</td>
            <td>Selected Gen: {{selected_generation}}</td>
            <td @click='selectGeneration(1)'>next</td>
          </tr>
          <tr>
            <td>Credits: {{generations[selected_generation].resources.credits}}</td>
            <td>Iron: {{generations[selected_generation].resources.iron}}</td>
            <td>Titanium: {{generations[selected_generation].resources.titanium}}</td>
          </tr>
          <tr>
            <td>Plants: {{generations[selected_generation].resources.greens}}</td>
            <td>Energy: {{generations[selected_generation].resources.energy}}</td>
            <td>Heat: {{generations[selected_generation].resources.heat}}</td>
          </tr>
        </table>
      </div>

    </div> 

  </div>

</template>

<script>
import ResourceCounter from "./components/ResourceCounter.vue";

export default {
  name: "TM_Resource_Tracker",
  components: {
    ResourceCounter,
  },

  data() {
    return {
      min_credits_production: -5,
      rating: 20,
      costs: {
        heatToTemp: 8,
        greensToOxygen: 8,
      },
      resources: {
        credits: 0,
        iron: 0,
        titanium: 0,
        greens: 0,
        energy: 0,
        heat: 0,
      },
      production: {
        credits: 0,
        iron: 0,
        titanium: 0,
        greens: 0,
        energy: 0,
        heat: 0,
      },
      generations: [],
      current_generation: 1,
      selected_generation:1,
    };
  },

  methods: {

    selectGeneration(inc){
      
      const selected = this.selected_generation + inc
      if(selected < 0){
        return
      }
      if(selected > this.generations.length-1){
        return
      }
      this.selected_generation = selected

    },
    nextGeneration() {
      this.generations.push({
        generation: this.current_generation,
        resources: {...this.resources},
        production: {...this.production},
        rating: this.rating,
      });

      this.current_generation++;
      this.resources.credits +=
        this.production.credits + this.rating;
      this.resources.iron += this.production.iron;
      this.resources.titanium += this.production.titanium;
      this.resources.greens += this.production.greens;
      this.energyToHeat()
      this.resources.energy += this.production.energy;
      this.resources.heat += this.production.heat;
      this.selected_generation++
    },
    resetGame() {
      this.resources.credits = 0;
      this.resources.iron = 0;
      this.resources.titanium = 0;
      this.resources.greens = 0;
      this.resources.energy = 0;
      this.resources.energy = 0;
      this.resources.heat = 0;

      this.production.credits = 0;
      this.production.iron = 0;
      this.production.titanium = 0;
      this.production.greens = 0;
      this.production.energy = 0;
      this.production.energy = 0;
      this.production.heat = 0;

      this.current_generation = 1
      this.selected_generation = 0
      this.rating = 20

      this.generations.push({
        generation: this.current_generation,
        resources: {...this.resources},
        production: {...this.production},
        rating: this.rating,
      })

    },

    increaseResource(...args) {
      if (this.resources[args[0]] + args[1] <= 0) {
        this.resources[args[0]] = 0;
      } else {
        this.resources[args[0]] += args[1];
      }
    },
    increaseProduction(...args) {
      const min =
        args[0] == "credits" ? this.min_credits_production : 0;

      if (this.production[args[0]] + args[1] <= min) {
        this.production[args[0]] = min;
      } else {
        this.production[args[0]] += args[1];
      }
    },
    greensToOxygen() {
      if(this.resources.greens >= 8){
        this.resources.greens -= this.costs.greensToOxygen;
      }
    },
    heatToTemp() {
      if(this.resources.heat >= 8){
        this.resources.heat -= this.costs.heatToTemp;
      }
    },
    energyToHeat() {
      this.resources.heat += this.resources.energy
      this.resources.energy = 0
    },
  },
  mounted(){
    this.resetGame()
  }
};
</script>

